}
  
  
*{
    font-family: 'Montserrat', sans-serif;
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
}

button{
    border: none;
    background-color: inherit;
}

.text_priority{
    font-size: 14px;
    font-weight: bold;
    text-align: center;
  }
  
  .outer {
      position: relative;
      display: inline-block;
      margin: 0 15px;
  }
  
  .inner {
    position: absolute;
    white-space: nowrap;
    top: 50%;
    left: 50%;
  }
  
  .rotate {
    -webkit-transform:  translateX(-50%) translateY(-50%) rotate(-90deg);
            transform:  translateX(-50%) translateY(-50%) rotate(-90deg);
  }
h1{
    text-align: center;
    text-transform: uppercase;
    font-size: 23px;
    margin-bottom: 10px;
}
  


p{
    font-size: 12px;
}
  






/* START Modal Create */
div.modal_create{
    position: relative;
    background-color: #fff;
    border: #000 1px solid;
    padding: 30px 20px 20px 20px;
    min-width: 320px;
    border-radius: 5px;
  }
  
  div.modal_create button.close_modal{
    position: absolute;
    top:  10px;
    right: 20px;
    background-color: #fff;
    border: none;
    cursor: pointer;
  }
  
  div.modal_create button.close_modal svg{
    color: #c4c4c4;
    font-size: 25px;
  }
  
  div.modal_create form label{
    display: block;
    font-size: 12px;
    font-weight: 700;
    margin: 10px 0;
  }
  
  div.modal_create form input{
    width: 100%;
    height: 30px;
    padding: 5px 10px;
    margin-bottom: 10px;
    background: rgba(196, 196, 196, 0.5);
    border-radius: 2px;
    border: none;
  }
  
  div.modal_create form textarea{
    width: 100%;
    padding: 5px 10px;
    background: rgba(196, 196, 196, 0.5);
    border-radius: 2px;
    border: none;
    margin-bottom: 10px;
    font-size: 14px;
  }
  
  div.modal_create form select{
    width: 100%;
    height: 30px;
    padding: 5px 10px;
    background: rgba(196, 196, 196, 0.5);
    border-radius: 2px;
    border: none;
    margin-bottom: 10px;
  }
  
  div.modal_create form div.form_group{
    display: flex;
  }
  
  div.modal_create form div.form_group div.date_input,
  div.modal_create form div.form_group div.select_weekday{
    margin-right: 10px;
    width: 40%;
  }
  div.modal_create form div.form_group div.date_input input{
    padding: 5px 0px;
    font-size: 12px;
  }
  
  div.modal_create form div.form_group div.category_select,
  div.modal_create form div.form_group div.select_priority{
    flex: 1 1;
  }
  
  
  
  div.modal_create form button{
    display: block;
    background-color: #2E87F6;
    font-size: 12px;
    font-weight: 900;
    color: #fff;
    border: none;
    border-radius: 4px;
    width: 100px;
    height: 30px;
    margin: 15px auto 10px;
  }


  
  
  /* END Modal Create */

  @media screen and (max-width: 768px){

  }
/* START tooltip */
.tooltip{
    visibility: hidden;
    min-width: 220px;
    position: absolute;
    top: 135%;
    margin-left: -60px;
    z-index: 1;
    background-color: #fff;
    font-size: 10px;
    line-height: 15px;
    padding: 15px 10px;
    color: #000;
    border: #000 1px solid;
    border-radius: 5px;
    opacity: 0;
    transition: opacity 0.7s ease-in 0.5s;
  }
  
  .tooltip.left{
    left: 110%;
  }
  
  .tooltip.right{
    right: 0;
  }

  
  .tooltip::after {
    content: "";
    position: absolute;
    top: -8px;
    margin-left: -5px;
    width: 15px;
    height: 15px;
    background: #FFFFFF;
    border-right:1px solid #000;
    border-top:1px solid #000;
    -webkit-transform:rotate(-58deg) skewX(-20deg);
            transform:rotate(-58deg) skewX(-20deg);
  }
  
  .tooltip.right::after{
    right: 10%;
  }
  
  .tooltip.left::after{
    left: 10%;
  }
  
  
  
  .tooltip span.todo_count{
    display: block;
    font-size: 12px;
    margin-top: 10px;
    font-weight: 600;
  }

div.modal_details{
    position: relative;
    width: 600px;
    background-color: #fff;
    border: #000 1px solid;
    border-radius: 5px;
}

div.modal_details div.header{
    display: flex;
    height: 60px;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    border-radius: 5px 5px 0 0;
    border-bottom: #000 1px solid;
}

div.modal_details div.header h2{
    font-size: 20px;
    color: #fff;
}

div.modal_details div.header button{
    background-color: inherit;
    border: none;
}

div.modal_details div.header svg{
    cursor: pointer;
    color: #fff;
    font-size: 24px;
    margin-left: 10px;
}

div.modal_details div.content{
    width: 430px;
    margin: 20px auto;
}

div.modal_details div.content p{
    font-size: 14px;
}

@media screen and (max-width: 768px){
    div.modal_details{
        width: 340px;
    }

    div.modal_details div.content{
        width: 80%;
    }

    div.add_todo input{
        width: 70%;
    }
}
div.add_todo input{
    width: 75%;
    height: 30px;
    padding: 5px 10px;
    margin-right: 10px;
}

div.add_todo button{
    flex: 1 1;
    cursor: pointer;
    border: #000 1px solid;
    color: #fff;
    height: 30px;
}

div.add_todo{
    display: flex;
    margin: 20px 0 10px 0;
}

div.check_completed{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

div.check_completed input[type = "checkbox"]{
    margin-right: 5px;
}

div.check_completed span{
    font-size: 12px;
}

div.todo_container{
    background-color: #E8E8E8;
    border-radius: 4px;
    padding: 8px;
    margin-top: 10px;
    font-size: 10px;
    text-align: center;
}

div.todo_container div.todo_item{
    display: flex;
    text-align: left;
    padding: 5px 10px;
    background-color: #fff;
    border-radius: 4px;
    justify-content: space-between;
    align-items: center;
}

/* div.todo_container div.todo_item + div.todo_item{
    margin-top: 8px;
}  */

div.todo_container div.todo_item{
    font-size: 14px;
    margin-bottom: 8px;
}



div.todo_container div.hide_todo{
    display: none;
}

div.todo_container div.todo_item button, 
div.todo_container div.todo_item button svg{
    font-size: 18px;
    height: 18px;
    margin-left: 5px;
}

div.todo_container div.todo_item button svg:hover{
    cursor: pointer;
    color: red;
}

.week{
    display: grid;
    grid-template-columns:  repeat(auto-fit, minmax(400px, 1fr));
}

.card{
    margin-right: 10px;
    margin-bottom: 30px;
}

.priority_grid{
    display: grid;
    grid-template-columns: repeat(2,1fr);
}

.priority_grid div.card_priority{
    margin: 5px;
    border-radius: 5px;
    padding: 15px;
    height: 180px;
    color: #fff;
    width: 200px;
}

.priority_grid div div.card-item{
    position: relative;
    box-shadow: 1px 3px 4px 2px rgba(0, 0, 0, 0.25);
    border-radius: 3px;
    padding: 6px;
   
}
  
.priority_grid div div.card-item + div.card-item{
    margin: 10px 0;
}

.priority_grid div div.card-item div.info{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.priority_grid div div.card-item div.info div.due_date{
    background-color: rgb(222, 0, 0);
    box-shadow: 1px 3px 4px 2px rgba(0, 0, 0, 0.25);
    padding: 3px 5px;
    border-radius: 5px;
    font-size: 10px;
    color: #fff;
    margin-left: 2px;    
}

.priority_grid div div.card-item div.info svg{
    font-size: 12;
    margin-right: 5px;
}

.priority_grid div div.card-item:hover .tooltip{
    visibility: visible;
    opacity: 1;
}

.btn_add{
    cursor: pointer;
    border: none;
    background: blue;
    box-shadow: 1px 3px 4px 2px rgba(0, 0, 0, 0.25);
    color: white;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    position: fixed;
    bottom: 10px;
    right: 15px;
    font-size: 30px;
    font-weight: bold;  
}


.priority_grid div div.card-item.remove_hover .tooltip{
  display: none;
}

@media screen and (max-width: 1200px){
  .week{
    grid-template-columns: 2fr;
    grid-template-columns:  repeat(auto-fit, minmax(380px, 1fr));
    
  }

  .priority_grid div.card_priority{
    width: 170px;
  }
}

@media screen and (max-width: 992px){
    .week{
      grid-template-columns: 2fr;
      grid-template-columns:  repeat(auto-fit, minmax(50%, 2fr));
      
    }
}

@media screen and (max-width: 768px){
    .week{
      grid-template-columns: none;
    }
  
    .priority_grid div.card_priority{
      min-width: auto;
      max-width: auto;
    }
  
    .outer{
      display: none;
    }
  
    .text_priority{
      display: none;
    }
  
    .container_mobile_priority{
      flex: 1 1;
    }
  
    .tooltip{
      display: none;
    }
}
